<template>
  <div class="cc-wrap cc-pad-10" :style="{width: width}">
    <div class="cc-pad-10" :style="{'background-color':bgColor}">
      <div class="cc-title">{{dataObj[attr.name]}}</div>
      <div class="cc-data">{{dataObj[attr.value]}}</div>
      <div class="cc-unit">({{dataObj[attr.unit]}})</div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dataObj: {
      type: Object
    },
    bgColor: {
      type: String,
      default: "#dd8855"
    },
    width: {
      type: String,
      default: "100%"
    },
    attr: {
      type: Object,
      default() {
        return {
          name: "name",
          value: "value",
          unit: "unit"
        };
      }
    }
  }
};
</script>

<style scoped>
.cc-wrap {
  width: 100%;
  display: inline-block;
  color: #fff;
  vertical-align: top;
}
.cc-pad-10 {
  padding: 10px;
}
.cc-title {
  font-size: 18px;
  font-weight: bold;
}
.cc-data {
  font-size: 16px;
  padding: 10px 0px;
  text-align: center;
  font-weight: bold;
}
.cc-unit {
  text-align: right;
}
</style>




