<template>
  <div class="car-wrap">
    <section v-if="dataLists.length>0" class="car-panel" :style="{width: width}" v-for="(item, index) in dataLists" :key="index">
      <div class="car-body" :style="{'background-color':varType(bgColor)===String?bgColor:bgColor[index%bgColor.length],height:height}">
        <slot :data="item" :pic="{up,down}">
          <div class="car-left">
            <span class="car-circle">{{item[attr.name]}}</span>
          </div>
          <div class="car-data" v-if="attr.value">{{parseFloat(item[attr.value]||0).toFixed(item[attr.num]||2)}}</div>
          <aside class="car-unit" v-if="attr.unit">({{item[attr.unit]||"-"}})</aside>
          <div class="car-ratio" v-if="attr.ratio">同比
            <span>{{parseFloat(Math.abs(item[attr.ratio])).toFixed(item[attr.num]||2)}}%</span><img :src="item[attr.ratio]==-1?'':(item[attr.ratio]>0?up:down)">
          </div>
        </slot>
        <slot name="add" :data="item" :pic="{up,down}"></slot>
      </div>
      <h5 v-if="showTitle&&item.name" class="car-title">{{item.name}}</h5>
    </section>
    <section v-if="dataLists.length===0" class="car-panel car-panel-no-data">
      <div class="car-body" :style="{'background-color':varType(bgColor)===String?bgColor:bgColor[0],height:height,'line-height': height}">暂无数据</div>
    </section>
  </div>
</template>
<script>
import { varType } from "@/util/util.js";
import up from "@/assets/images/up.png";
import down from "@/assets/images/down.png";
export default {
  props: {
    width: {
      type: String,
      default: "100%"
    },
    bgColor: {
      type: [String, Array],
      default() {
        return [
          // "#0C7E0E",
          "#426896",
          "#AF399C",
          "#ca4a56",
          "#6e43d2",
          "#3B65BF",
          "#177D86",
          "#2D004E",
          "#00D8CC",
          "#004060",
          "#4E0038",
          "#33CC00",
          "#E36B19"
        ];
      }
    },
    dataLists: {
      type: Array
    },
    showTitle: {
      type: Boolean,
      default: false
    },
    // 每个框的高度
    height: {
      type: String
    },
    attr: {
      type: Object,
      default() {
        return {
          name: "word",
          value: "data",
          unit: "unit",
          ratio: "ratio",
          num: "" //保留小数位
        };
      }
    }
  },
  data() {
    return {
      up,
      down
    };
  },
  methods: {
    varType
  }
};
</script>
<style scoped>
.car-wrap {
  margin: -10px -10px 0 0;
}
.car-panel {
  text-align: center;
  min-width: 150px;
  font-size: 18px;
  padding: 10px 10px 0 0;
  display: inline-block;
  vertical-align: top;
}
.car-panel-no-data {
  /* height: 40px; */
}
.car-left {
  text-align: left;
}
.car-body {
  /* border: 1px solid #78adfe; */
  padding: 10px;
  color: #fff;
}
.car-circle {
  /* background: peachpuff; */
  /* padding: 10px 30px; */
  /* border-radius: 50%; */
  display: inline-block;
  font-size: 20px;
  font-weight: bold;
}
.car-title {
  font-size: 20px;
  font-weight: bold;
}
.car-data {
  font-weight: bold;
  color: #fb5f00;
}
.car-unit {
  height: 24px;
}
.car-ratio span {
  color: #fff;
  font-size: 22px;
}
</style>



