<template>
  <div>
    <da-panel title="大气污染物排放量统计" ref="panelCount">
      <da-time-select slot="search" v-model="time" :show-time="[1,0]" @change="changeTime"></da-time-select>
      <carbon-data class="ce_25p" :data-lists="item"></carbon-data>
      <p class="ce_pad_20">本期综合能耗为 484.5吨 标煤，综合节能量为 23.55吨 标煤。</p>
      <p class="ce_pad_20">每消耗1吨标煤 = 排放 2.493吨 二氧化碳 + 0.0165吨 二氧化硫 + 0.0156吨 氮氧化合物 + 0.0096吨 烟尘。</p>
    </da-panel>
    <da-panel title="大气污染物排放量趋势图" ref="panelChart">
      <da-radio-select slot="search" v-model="contaminant" label="name" value-key="value" :option-lists="contaminants" @change="changeCont"></da-radio-select>
      <da-line-chart id="lineChart" :date-time="time" label="label" value-key="data" x-axis-key="time" :data-lists="chartData"></da-line-chart>
    </da-panel>
    <da-panel title="减少大气污染物排放量统计" ref="panelData">
      <div class="ce_flex">
        <div class="ce_gauge_wrap">
          <da-gauge-chart id="gaugeChart" title="碳排放量" height="210px" :date-time="time" :data-lists="reduceChartData"></da-gauge-chart>
        </div>
        <div class="ce_w_auto">
          <p>节约 1 度电 = 减排 0.272 千克“碳”</p>
          <p>节约 1 千克原煤 = 减排 0.486 千克“碳”</p>
          <p>节约 1 千克标准煤 = 减排 0.68 千克“碳”</p>
        </div>
        <div class="ce_cont_wrap">
          <carbon-count class="ce_cont_layo" v-for="(item, index) in reduceData" width="50%" :key="index" :bg-color="bgColor[index]" :data-obj="item"></carbon-count>
        </div>
      </div>
    </da-panel>
  </div>
</template>
<script>
import carbonData from "./components/carbonData";
import carbonCount from "./components/carbonCount";
export default {
  components: {
    carbonData,
    carbonCount
  },
  data() {
    return {
      time: "2017-10",
      contaminant: "",
      carbonData: [],
      contaminants: [],
      chartData: [],
      reduceChartData: {},
      reduceData: [],
      bgColor: ["#548630", "#54ddde", "#456dca", "#a578d3"]
    };
  },
  methods: {
    changeTime(time) {
      //   时间切换时调用方法 ;
      this.getCarbon();
      this.getChart();
      this.getReduceChart();
      this.getReduceData();
    },
    changeCont(value) {
      this.getChart();
    },
    // 获取统计数据
    getCarbon() {
      // this.$ajax.get("",{params:{},loadingTarget:this.$refs.panelCount})
      let name = ["二氧化碳", "二氧化硫", "氮氧化合物", "烟尘"];
      let word = ["CO₂", "SO₂", "NOX", "Smoke"];
      let data = [];
      this.$each(name, (item, index) => {
        data.push({
          name: item,
          word: word[index],
          data: (Math.random() * 300).toFixed(2),
          ratio: (5 - Math.random() * 10).toFixed(2),
          unit: "吨"
        });
      });
      this.carbonData = data;
    },
    // 获取趋势图数据
    getChart() {
      // 数据请求
      // this.$ajax.get("",{params:{},loadingTarget:this.$refs.panelChart}).then(res=>{this.chartData=res.data.records})
      let data = [];
      let i = 0;
      let name = ["二氧化碳", "二氧化硫", "氮氧化合物", "烟尘"];
      while (i < 30) {
        data.push({
          time: this.time + "-" + (i + 1),
          label: name[this.contaminant],
          data: parseInt(Math.random() * 100)
        });
        i++;
      }
      this.chartData = data;
    },
    // 减少所有大气污染物排放量统计chart
    getReduceChart() {
      this.reduceChartData = {
        min: 0,
        max: parseInt(1000 + Math.random() * 1000),
        value: Math.random() * 1000,
        unit: "吨(t)"
      };
    },
    // 减少各种污染物的排放量
    getReduceData() {
      let word = ["CO₂", "SO₂", "NOX", "Smoke"];
      let data = [];
      this.$each(word, (item, index) => {
        data.push({
          name: item,
          value: (Math.random() * 300).toFixed(2),
          unit: "吨"
        });
      });
      this.reduceData = data;
    }
  },
  mounted() {
    setTimeout(() => {
      this.contaminants = [
        {
          name: "CO₂(二氧化碳)",
          value: 0
        },
        {
          name: "SO₂(二氧化硫)",
          value: 1
        },
        {
          name: "NOX(氮氧化合物)",
          value: 2
        },
        {
          name: "Smoke(烟尘)",
          value: 3
        }
      ];
      this.contaminant = 2;
      this.getCarbon();
      this.getChart();
      this.getReduceChart();
      this.getReduceData();
    }, 200);
  }
};
</script>

<style scoped>
.ce_25p {
  width: 25%;
  display: inline-block;
}
.ce_pad_20 {
  padding: 0px 20px;
}
.ce_pad_20:last-child {
  padding-bottom: 10px;
}
.ce_cont_layo {
  padding: 10px;
}

.ce_flex {
  display: flex;
  align-items: flex-end;
}
.ce_gauge_wrap {
  display: inline-block;
  width: 400px;
  padding: 10px;
}
.ce_w_auto {
  display: inline-block;
  width: 270px;
  line-height: 30px;
}
.ce_cont_wrap {
  flex: 1;
  display: inline-block;
}

@media screen and (max-width: 1200px) {
  .ce_flex {
    display: block;
  }
  .ce_cont_wrap {
    display: block;
  }
}
</style>



